export const MainWidget: React.FC<PropsWithChildren> = () => {

    const [open, setOpen] = React.useState(false);
    const handleClose = () => {
        setOpen(false);
    };
    const handleToggle = () => {
        setOpen(!open);
    };

    return (
        <MainStyle>
            <div className="text">
                <h2>Get a Spot on the Whitelist!</h2>
                <h3>Connect your Wallet and get a chance to get whitelisted for the minting event on Sept 1st.</h3>

                <Tooltip title="Coming Soon" arrow>
                    <div onClick={() => handleToggle()} className="button">
                        PRE-register
                    </div>
                </Tooltip>
                <div className='street_sign'>
                    <p className="title">Coming Soon</p>
                    <img src={street_sign} alt="" />
                </div>


            </div>

            <VideoWidget />
            <Backdrop
                style={{
                    zIndex: 99
                }}
                open={open}
                onClick={handleClose}
            >
                {/* <CircularProgress color="inherit" /> */}
                <ComingStyle>Coming Soon</ComingStyle>
            </Backdrop>
        </MainStyle>
    )
}

